<template>
<div class="container">
  <br>
  <h1 style="text-align:center"></h1>
  <hr>
  <div class="row">

    <div class="col-md-4" style="text-align:center;padding:1em 0;">
        <h3><a style="text-decoration:none;"></a><span style="color:gray;"><br /></span></h3>
        <iframe src="http://free.timeanddate.com/clock/i5h4olht/n236/tlcn8/fn6/fs16/tt0/tm3/th1/ta1/tb4" frameborder="0" width="144" height="45"></iframe>
    </div>

    <div class="col-md-4" style="text-align:center;padding:1em 0;">
      <h3>
        <a style="text-decoration:none;"></a>
        <span style="color:gray;"><br/>{{authUser.region}}</span></h3>
      </h3>
    </div>

    <div class="col-md-4" style="text-align:center;padding:1em 0;">
      <h3><a style="text-decoration:none;"></a><span style="color:gray;"><br />()</span></h3>
      <h6>{{simTimeStart}}</h6>
    </div>

    <br>

    <div class="panel panel-default" >
      <div class="panel-body" >
        <div class="col-md-12">
          <center>
            <h3></h3>
            <p></p>
            <br>
              <button v-on:click="recordTime" class="btn btn-primary btn-block " id="register"></button>
            <br>
            <a href="apchinesedebate.com/user_dashboard" class="btn btn-lg"></a>
          </center>
        </div>
      </div>
    </div>
    
  </div>
</div>
</template>
<script>
import axios from 'axios';

export default
{
  name:'lottery',
  created () {
    setInterval(() => this.now = new Date, 1000 * 60)
  },
  data(){
    return{
      pressed: 0,
      simTimeStart: 0
    }
  },
  computed: {
    now () {
      return new Date
    },
    authUser: function(){ if(this.$store.getters.authUser) return JSON.parse(this.$store.getters.authUser);},
  },
  created(){
    this.startTime();
  },
  methods:{
    recordTime:function(){
      const data = { 'pressed' : 1} //pressed here is to notify backend that user pressed the button
      try{
        axios
          .put('api/time/simulation/store', data, {
            headers: { Authorization: "Bearer " + localStorage.getItem('token')}
          })
          .then(resp=>{
            if(resp.data.status == 200){
              //console.log(resp.data)
              alert("")
            }else if(resp.data.status == 304){ alert("")}
          })
      }catch(e){console.log(e)
      }
    },
    startTime: function(){
      axios
        .get('api/time/simulation/start',{
          headers:{
              Authorization: "Bearer " + localStorage.getItem('token')
          }
        })
        .then(resp=>{
          //console.log(resp.data)
          if(resp.data.status == 200){
              this.simTimeStart = resp.data.simTimeStart;
          }
        })
    },
  }
}
</script>
<style scoped>
  .register{
    background-color: darkred
  }
</style>



